import React from "react";
import { Button, Input, Modal, ModalBody, ModalHeader } from "reactstrap";

function RequestModal(props) {
    const { open, onToggle } = props;
    return (
        <div className="request-modal">
            <Modal isOpen={open} toggle={onToggle}>
                <div className="p-3">
                    <ModalHeader toggle={onToggle}>Yu cu lin h</ModalHeader>
                    <ModalBody>
                        <div>Yu cu ngi ng tin lin h theo thng tin di y</div>
                        <Input className="mt-3" placeholder="H v tn" />
                        <Input className="mt-3" placeholder="S in thoi" />
                        <h6 className="mt-3">Li nhn</h6>
                        <Input type="textarea" value={"ti quan tm n bt ng sn ny"} />
                        <Button color="danger" className="mt-5 w-100">
                            Gi yu cu
                        </Button>
                    </ModalBody>
                </div>
            </Modal>
        </div>
    );
}

export default RequestModal;
