import React from 'react';
import { Phone } from 'react-bootstrap-icons';
import { Button, Input } from 'reactstrap';

function LoginView({ setViewMode }) {
    return (
        <div className='p-4'>
            <h6>Xin cho bn</h6>
            <h5>ng nhp</h5>
            <Input className="mt-3" type="email" placeholder="Nhp email" />
            <Input className="mt-3" type="password" placeholder="Nhp mt khu" />
            <Button className='w-100 mt-3' primary>ng nhp</Button>
            <p className='mt-3'>
                Cha c ti khon?{" "}
                <a color='info' href="#" onClick={() => setViewMode('signup')}>ng k</a>{" "}
            </p>
        </div>
    );
}

export default LoginView;