import React, { useState } from "react";
import {
    BoxSeam,
    Buildings, ChevronDown,
    ChevronUp, Gear, GraphUpArrow,
    ListUl,
    Paperclip
} from "react-bootstrap-icons";

function SideMenu(props) {
    const menuItem = [
        {
            title: "Qun l tin ng",
            icon: <ListUl size={22} />,
            children: [{ title: "ng mi" }, { title: "Danh sch tin" }, { title: "Tin nhp" }, { title: "Qun l tin ti tr" }],
        },
        {
            title: "Ti khon Pro",
            icon: <BoxSeam size={22} />,
            children: [{ title: "ng k mua" }],
        },
        {
            title: "Qun l ti chnh",
            icon: <GraphUpArrow size={22} />,
            children: [
                { title: "Thng tin s d" },
                { title: "Lch s giao dch" },
                { title: "Nhm khuyn mi" },
                { title: "Np tin vo ti khon" },
                { title: "Qun l im tch lu" },
            ],
        },
        {
            title: "Qun l TK Doanh nghip",
            icon: <Buildings size={22} />,
            children: [{ title: "ng k ti khon Doanh nghip" }],
        },
        {
            title: "Bo gi &  Hng dn",
            icon: <Paperclip size={22} />,
            children: [{ title: "Bo gi" }, { title: "Hng dn thanh ton" }, { title: "Hng dn s dng" }],
        },
        {
            title: "Tin ch",
            icon: <Gear size={22} />,
            children: [{ title: "Thng bo" }, { title: "Qun l ng k nhn email" }, { title: "Yu cu xo ti khonrnf" }],
        },
    ];

    const [selectedMenu, setSelectedMenu] = useState([]);
    const checkActiveMenu = (id) => selectedMenu.indexOf(id) !== -1;
    const handleClickMenu = (id) => {
        if (checkActiveMenu(id)) {
            const newItems = selectedMenu.filter((item) => item !== id);
            setSelectedMenu(newItems);
        } else {
            setSelectedMenu([...selectedMenu, id]);
        }
    };
    return (
        <div className="side__menu px-3 pt-2">
            {menuItem.map((item, index) => (
                <div className={`menu__item ${checkActiveMenu(index) ? "menu__item--active" : ""}`} key={index}>
                    <div className={`menu__item-main d-flex justify-content-between align-items-center `} onClick={() => handleClickMenu(index)}>
                        <div className="d-flex">
                            <div className="me-3 menu__icon">{item.icon}</div>
                            <div>{item.title}</div>
                        </div>
                        {checkActiveMenu(index) ? <ChevronUp /> : <ChevronDown />}
                    </div>
                    {item.children &&
                        item.children.map((children, indexChild) => (
                            <div className="menu__item-sub d-flex align-items-center" key={indexChild}>
                                <div className="me-3 menu__icon">{item.icon}</div>
                                <div>{children.title}</div>
                            </div>
                        ))}
                </div>
            ))}
        </div>
    );
}

export default SideMenu;
