import React from 'react';
import { Phone } from 'react-bootstrap-icons';
import { Button, Input } from 'reactstrap';

function SignupView({ setViewMode }) {
    return (
        <div className='p-4'>
            <h6>Xin cho bn</h6>
            <h5>ng k ti khon mi</h5>
            <Input className="mt-3" type="email" placeholder="Nhp email" />
            <div className='d-flex align-items-center mt-3'>
                <Button disabled>
                    <Phone />
                </Button>
                <Input type="number" placeholder="Nhp s in thoi" />
            </div>
            <Input className="mt-3" type="password" placeholder="Nhp mt khu" />
            <Input className="mt-3" type="password" placeholder="Nhp li mt khu" />
            <Button className='w-100 mt-3' primary>Tip tc</Button>
            <p className='mt-3'>
                Bng vic tip tc, bn ng  vi{" "}
                <a color='info' href="#">iu khon s dng</a>{" "}
                ca chng ti
            </p>
            <p className='mt-3'>
                 c ti khon?{" "}
                <a color='info' href="#" onClick={() => setViewMode('login')}>ng nhp</a>{" "}
            </p>
        </div>
    );
}

export default SignupView;